:root {
  --f-primary: 'Commissioner';
  --font-size: 16px;
  --c-modarate-cyan: hsl(176deg 50% 47%);
  --c-dark-cyan: hsl(176deg 72% 28%);
  --c-black: hsl(0deg 0% 0%);
  --c-dark-gray: hsl(0deg 0% 48%);
  --c-light-gray: hsl(0deg 0% 85%);
  --c-lighten-gray: hsl(0deg 0% 95%);
  --c-white: #fff;
  --c-red: #bf2121;
}

